<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process115"></a>

<p><br></p>
<p><span class="rvts17">被困的精灵（</span><span class="rvts50">The Imprisoned Elf</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:488,Y:549</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：萨西拉（</span><span class="rvts20">Saheila</span><span class="rvts21">）</span>
<br><span class="rvts20">X:204,Y:131</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：阿米罗（</span><span class="rvts20">Amyro</span><span class="rvts21">）</span>
<br><span class="rvts20">X:203,Y:138</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：格里夫（</span><span class="rvts20">Griff</span><span class="rvts21">）</span>
<br><span class="rvts21">以上三个坐标</span><span class="rvts20">NPC</span><span class="rvts21">中的任何一人都可触发该任务。</span>
<br><span class="rvts27"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">拯救被冤枉的阿米罗</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1021.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">该任务可以在山洞种找到萨西拉接到。阿米罗是萨西拉的挚爱，被欢乐堡厨房区的霸主格里夫抓取，具体原因是因为格里夫丢了一个大的品种橘子。</span>
<br><span class="rvts21">起始阿米罗只是偷了一些简单的蔬菜和面食，并没有偷什么橘子。橘子是一个奸商叫做斯汀泰尔（</span><span class="rvts20">Stingtail</span><span class="rvts21">）的蜥蜴给偷走的。</span>
<br><span class="rvts21">斯汀泰尔就是出售火元素法术的商人，因此按照比较公道的做法，我们会去干掉斯汀泰尔，但在那之前最好把他的技能书给偷走或买到。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1022.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">由于斯汀泰尔比较嗜睡，因此只需队伍中有一个盗贼，便可以偷走他身上的大橘子。</span>
<br><span class="rvts21">这里需要注意的是，尽可能避免队伍离有塞碧莉，因为塞碧莉的主线任务中，有刺杀斯汀泰尔的这一环节。</span>
<br><span class="rvts21">如果斯汀泰尔被刺杀，那么大橘子的事情将失去人证，阿米罗只能通过暴力手段（杀死格里夫和他的同僚）来获得好的结局。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1023.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">另外，会发现在厨房附近，有一只到处流窜的老鼠，如果队伍里有角色拥有动物伙伴（</span><span class="rvts20">Pet Pal</span><span class="rvts21">）天赋，那么可以通过和老鼠交谈，而得知橘子是被斯汀泰尔给偷走的。除了这只老鼠，其它任何人都证明不了阿米罗的清白。如果斯汀泰尔如果死于非命，将不会掉落大橘子。（无论是被谁杀掉）</span>
<br><span class="rvts21">可以把大橘子带给格里夫，并揭发斯汀泰尔。之后，斯汀泰尔会被一名刺客给杀死。（如果速度快的话，会赶去并杀掉那个刺客，斯汀泰尔也会因此活下来）。</span>
<br><span class="rvts21">如果大橘子的事情不能正常解决（格里夫无法获得大橘子），那么拯救阿米罗的任务将会变为一场恶战。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



